  .main-container > .markdown > * {
    width: 100% !important;
  }
  .main-wrapper {
    width: 100%;
    border-radius: 0;
    margin: 0;
  }
  #footer {
    text-align: center;
  }
  #footer .footer-wrap {
    padding: 40px;
  }
  #footer .footer-center {
    text-align: center;
  }
  #footer h2 {
    margin-top: 16px;
  }
  #footer .bottom-bar {
    text-align: center;
  }
  #footer .bottom-bar .translate-button {
    width: auto;
    text-align: center;
    margin-bottom: 16px;
  }
  .prev-next-nav {
    margin-left: 16px;
    width: calc(100% - 32px);
  }
  .drawer .ant-menu-inline .ant-menu-item:after,
  .drawer .ant-menu-vertical .ant-menu-item:after {
    left: 0;
    right: auto;
  }
}
@media only screen and (max-width: 768px) {
  #logo {
    padding: 0;
    display: block;
    margin-left: 30px;
    margin-right: auto;
    float: none;
  }
  .header-meta {
    padding-right: 80px;
  }
  .home-banner-image {
    display: block;
  }
  .home-banner-anim {
    display: none;
  }
  .banner-wrapper {
    width: 80%;
    height: calc(100vh - 64px);
    overflow: hidden;
  }
  .banner-wrapper .banner-title-wrapper,
  .banner-wrapper .banner-image-wrapper {
    display: block;
    position: static;
    position: initial;
    width: 100%;
    height: auto;
  }
  .banner-wrapper .banner-title-wrapper {
    text-align: center;
    max-width: 480px;
  }
  .banner-wrapper .banner-title-wrapper .button-wrapper {
    text-align: center;
    display: block;
    margin-top: 5vh;
  }
  .banner-wrapper .banner-title-wrapper .button-wrapper a {
    display: inline-block;
  }
  .banner-wrapper .banner-title-wrapper .button-wrapper .github-btn {
    display: flex;
    margin: 20px auto;
    justify-content: center;
  }
  .banner-wrapper .banner-image-wrapper {
    margin: 10vh auto 5vh;
  }
  .home-page {
    width: 90%;
  }
  .home-code,
  .page-content {
    width: 100%;
  }
  #footer .footer-wrap {
    padding: 0;
  }
}
@media only screen and (max-width: 480px) {
  .page1 {
    height: 2400px;
  }
  .page2 {
    height: 628px;
  }
  .page1-box-wrapper {
    display: block;
  }
  .page1-box-wrapper li {
    width: 80%;
    display: block;
    margin: 0 auto 100px;
  }
  .page1-box-wrapper li .page1-box {
    width: 100%;
  }
  .banner-wrapper .banner-title-wrapper h1 {
    font-size: 28px;
  }
  .banner-wrapper .banner-title-wrapper p {
    font-size: 16px;
  }
}
@media only screen and (max-width: 320px) {
  .home-page h2 {
    font-size: 24px;
  }
  .page2 {
    height: 648px;
  }
}

